import React, {Component} from "react";
import {Button, Card, message, Steps} from "antd";

import "./index.css";
import AddNew from "../../../components/Widgets/AddNew";
import OtherFormControls from "./OtherFormControls";

const Step = Steps.Step;

const steps = [{
  title: 'Patient',
  content: '',
}, {
  title: 'Consultation',
  content: '',
}, {
  title: 'Suivi',
  content: '',
}];

class SwitchStep extends Component {
  constructor(props) {
    super(props);
    this.state = {
      current: 0,
    };
  }

  next() {
    const current = this.state.current + 1;
    this.setState({current});
  }

  prev() {
    const current = this.state.current - 1;
    this.setState({current});
  }

  render() {
    const {current} = this.state;
    return (
      <div >
        <Steps current={current}>
          {steps.map(item => <Step key={item.title} title={item.title}/>)}
        </Steps>
        <div className="gx-py-5">
          <OtherFormControls />
          {steps[this.state.current].content}
        </div>
        <div className="steps-action">
          {
            this.state.current < steps.length - 1
            &&
            <Button type="primary" onClick={() => this.next()}>Suivant</Button>
          }
          {
            this.state.current === steps.length - 1
            &&
            <Button type="primary" onClick={() => message.success('Processing complete!')}>Terminer</Button>
          }
          {
            this.state.current > 0
            &&
            <Button style={{marginLeft: 8}} onClick={() => this.prev()}>
              Précédent
            </Button>
          }
        </div>
      </div>
    );
  }
}


export default SwitchStep;
